/* Container */
.login-container {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f3f4f6;
    padding: 1rem;
  }
  
  /* Box */
  .login-box {
    background-color: white;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    border-radius: 1rem;
    width: 100%;
    max-width: 400px;
    padding: 1.5rem;
  }
  
  /* Role toggle buttons */
  .role-buttons {
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
  }
  
  .role-button {
    padding: 0.5rem 1rem;
    font-weight: 600;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .role-button.admin {
    border-radius: 9999px 0 0 9999px;
  }
  
  .role-button.player {
    border-radius: 0 9999px 9999px 0;
  }
  
  .role-button.active {
    background-color: #2563eb;
    color: white;
  }
  
  .role-button.inactive {
    background-color: #e5e7eb;
  }
  
  /* Form inputs */
  .login-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .login-form label {
    font-size: 0.875rem;
    font-weight: 500;
    color: #374151;
  }
  
  .login-form input {
    padding: 0.5rem 0.75rem;
    border: 1px solid #d1d5db;
    border-radius: 0.375rem;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  }
  
  .login-form input:focus {
    outline: none;
    border-color: #2563eb;
    box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.3);
  }
  
  /* Submit button */
  .login-button {
    background-color: #2563eb;
    color: white;
    font-weight: bold;
    padding: 0.5rem;
    border-radius: 0.375rem;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .login-button:hover {
    background-color: #1e40af;
  }
  